.alabom-game-style {
  position: absolute;
  width: 600px;
  height: 400px;
  background-color: white;
  clip: rect(0px, 600px, 400px, 0px); }

.alabom-game-style-empty {
  position: absolute;
  width: 100%;
  height: 100%; }

.alabom-game-engine-mute {
  position: absolute;
  top: 5px;
  right: 5px;
  width: 43px;
  height: 41px;
  background-position: 0 0;
  background-size: calc(399px / 4.5) calc(182px / 4.5) !important;
  background-repeat: no-repeat !important;
  cursor: pointer;
  background: url(/common/mute.png); }

.alabom-game-engine-help {
  position: absolute;
  left: 0px;
  bottom: 0px;
  width: 600px;
  height: 100px;
  clip: rect(0px, 600px, 400px, 0px); }

.alabom-game-engine-help-text {
